import axios from "axios";
import React, { useEffect, useState } from "react";

export default function Home() {
  const [trendingMovies, settrendingMovies] = useState([]);
  const [trendingtvshows, settrendingtvshows] = useState([]);
  const [trendingpeople, settrendingpeople] = useState([]);
  let imgprefix = "https://image.tmdb.org/t/p/w500";

  async function getTrending(media_type, callback) {
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/trending/${media_type}/week?api_key=fd292faec88bb4e94368527e7aba899b`
    );
    callback(data.results.slice(0, 10));
  }
  useEffect(() => {
    getTrending("movie", settrendingMovies);
    getTrending("tv", settrendingtvshows);
    getTrending("person", settrendingpeople);
  }, []);

  return (
    <div className="home">
      <div className="row ">
        <div className="col-md-4 d-flex align-items-center col-sm-6">
          <div>
            <div className="w-25 my-2 brdr"></div>
            <h2 className="h3">
              Trending Movies <br />
              To Watch <br />
              Right now
              <p className="text-muted">trending movies to watch</p>
            </h2>
            <div className="w-100 brdr"></div>
          </div>
        </div>
        {trendingMovies.map((movie, index) => (
          <div key={index} className="col-md-2 col-sm-6">
            <img
              className="w-100 py-4"
              src={imgprefix + movie.poster_path}
              alt={movie.title}
            />
            <h3 className="h6">{movie.title}</h3>
          </div>
        ))}
      </div>

      <div className="row my-5 py-5">
        <div className="col-md-4 d-flex align-items-center col-sm-6">
          <div>
            <div className="w-25 my-2 brdr"></div>
            <h2 className="h3">
              Trending Tvshows <br />
              To Watch <br />
              Right now
              <p className="text-muted">trending Tvshows to watch</p>
            </h2>
            <div className="w-100 brdr"></div>
          </div>
        </div>
        {trendingtvshows.map((tvshow, index) => (
          <div key={index} className="col-md-2 col-sm-6">
            <img
              className="w-100 py-4"
              src={imgprefix + tvshow.poster_path}
              alt={tvshow.name}
            />
            <h3 className="h6">{tvshow.name}</h3>
          </div>
        ))}
      </div>

      <div className="row my-5 py-5">
        <div className="col-md-4 d-flex align-items-center col-sm-6">
          <div>
            <div className="w-25 my-2 brdr"></div>
            <h2 className="h3">
              Trending people <br />
              To Watch <br />
              Right now
              <p className="text-muted">trending people to watch</p>
            </h2>
            <div className="w-100 brdr"></div>
          </div>
        </div>
        {trendingpeople.map((person, index) => (
          <div key={index} className="col-md-2 col-sm-6">
            <img
              className="w-100 py-4"
              src={imgprefix + person.profile_path}
              alt={person.name}
            />
            <h3 className="h6">{person.name}</h3>
          </div>
        ))}
      </div>
    </div>
  );
}
