import Joi from "joi";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function Login(props) {
  const route = useNavigate();
  const [ErorList, setErorList] = useState([]);
  const [User, setUser] = useState({
    email: "",
    password: "",
  });

  function userInfo(e) {
    let myUser = { ...User };
    myUser[e.target.name] = e.target.value;
    setUser(myUser);
  }

  function submit(e) {
    e.preventDefault();
    let validation = validateRegister();

    if (validation.error) {
      setErorList(validation.error.details);
    } else {
      localStorage.setItem("email", User.email);
      props.logUser();

      route("/home");
    }
  }

  function validateRegister() {
    const schema = Joi.object({
      email: Joi.string()
        .email({ tlds: { allow: ["com", "net", "org"] } })
        .required(),
      password: Joi.string().pattern(new RegExp("^[0-9]{1,9}$")),
    });
    return schema.validate(User, { abortEarly: false });
  }

  return (
    <div className="login w-75 mx-auto py-5">
      <div className="alert alert-success text-center p-2">
        for testing :use any valid email with any numbers for password
      </div>
      <h1 className="text-center">Login</h1>
      <br />
      {ErorList.map((eror, index) => (
        <div key={index} className="alert alert-danger p-1">
          {" "}
          {eror.message}
        </div>
      ))}
      <form onSubmit={submit}>
        <div className="py-2">
          {" "}
          <label htmlFor="email">Email</label>
          <input
            type="email"
            name="email"
            className="form-control"
            onChange={userInfo}
          />
        </div>
        <div className="py-2">
          {" "}
          <label htmlFor="password">Password</label>
          <input
            type="password"
            name="password"
            className="form-control"
            onChange={userInfo}
          />
        </div>
        <button type="submit" className="btn btn-danger my-4">
          {" "}
          Login
        </button>
      </form>
    </div>
  );
}
