import axios from "axios";
import React, { useEffect, useState } from "react";

export default function Movies() {
  let imgprefix = "https://image.tmdb.org/t/p/w500";
  const [trendingMovies, settrendingMovies] = useState([]);
  async function getTrending() {
    let { data } = await axios.get(
      `https://api.themoviedb.org/3/movie/top_rated?api_key=fd292faec88bb4e94368527e7aba899b`
    );
    settrendingMovies(data.results);
  }
  useEffect(() => {
    getTrending();
  }, []);

  return (
    <div className="movies">
      <h2 className="text-center my-5">Top Rated Movies</h2>
      <div className="row">
        {trendingMovies.map((tvshow, index) => (
          <div key={index} className="  col-md-2 col-sm-6 ">
            <img
              className="w-100 py-4"
              src={imgprefix + tvshow.poster_path}
              alt={tvshow.title}
            />
            <h3 className="h6">{tvshow.title}</h3>
          </div>
        ))}
      </div>
    </div>
  );
}
