import Joi from "joi";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function Register() {
  const route = useNavigate();
  const [User, setUser] = useState({
    frist_name: "",
    last_name: "",
    email: "",
    password: "",
    age: "",
  });
  const [ErorList, setErorList] = useState([]);

  function getUserInfo(e) {
    let myUser = { ...User };
    myUser[e.target.name] = e.target.value;
    setUser(myUser);
  }

  function submit(e) {
    e.preventDefault();
    let validation = validateRegister();

    if (validation.error) {
      setErorList(validation.error.details);
      console.log(ErorList);
    } else {
      route("/login");
    }
  }

  function validateRegister() {
    const schema = Joi.object({
      frist_name: Joi.string().min(3).max(10).required(),
      last_name: Joi.string().min(3).max(10).required(),
      email: Joi.string()
        .email({ tlds: { allow: ["com", "net", "org"] } })
        .required(),
      password: Joi.string().pattern(new RegExp("^[0-9]{1,9}$")),
      age: Joi.number().min(16).max(80).required(),
    });
    return schema.validate(User, { abortEarly: false });
  }

  return (
    <div className="register w-75 mx-auto py-3">
      <h1 className="text-center mt-5 mb-3 ">Register Now</h1>
      <br />
      {ErorList.map((eror, index) => (
        <div key={index} className="alert alert-danger p-1">
          {" "}
          {eror.message}
        </div>
      ))}
      <form onSubmit={submit}>
        <div className="my-3">
          {" "}
          <label htmlFor="frist_name"> Frist Name</label>
          <input
            onChange={getUserInfo}
            type="text"
            name="frist_name"
            className="form-control  my-2"
          />
        </div>
        <div className="my-3">
          {" "}
          <label htmlFor="last_name"> Last Name</label>
          <input
            onChange={getUserInfo}
            type="text"
            name="last_name"
            className="form-control  my-2"
          />
        </div>
        <div className="my-3">
          {" "}
          <label htmlFor="email"> Email</label>
          <input
            onChange={getUserInfo}
            type="email"
            name="email"
            className="form-control  my-2"
          />
        </div>
        <div className="my-3">
          <label htmlFor="password"> Password</label>
          <input
            onChange={getUserInfo}
            type="password"
            name="password"
            className="form-control  my-2"
          />
        </div>
        <div className="my-3">
          {" "}
          <label htmlFor="age">Age</label>
          <input
            onChange={getUserInfo}
            type="text"
            name="age"
            className="form-control  my-2"
          />
        </div>

        <button type="submit" className="btn btn-danger py-2 my-2">
          Register
        </button>
      </form>
    </div>
  );
}
