import React from "react";
import { NavLink } from "react-router-dom";

export default function Navbar(props) {
  return (
    <div className="navbar d-flex justify-content-between align-items-center p-3">
      <ul className="d-flex align-items-center">
        <li className="pe-2 fw-bold fs-2 ">
          <p>NOXE</p>
        </li>
        {props.loginuser ? (
          <>
            <li className="px-2">
              <NavLink to="/home">Trending</NavLink>
            </li>

            <li className="px-2">
              <NavLink to="/movies"> Movies</NavLink>
            </li>
            <li className="px-2">
              <NavLink to="/tv">Tv</NavLink>
            </li>
            <li className="px-2">
              <NavLink to="/gellary">Gellary</NavLink>
            </li>
          </>
        ) : (
          ""
        )}
      </ul>
      <ul className="d-flex align-items-center">
        <li className="px-2">
          <a href="http://google.com">
            <i className="fa-brands fa-facebook"></i>
          </a>
        </li>
        <li className="px-2">
          <a href="http://google.com">
            <i className="fa-brands fa-instagram"></i>
          </a>
        </li>
        <li className="px-2">
          <a href="http://google.com">
            <i className="fa-brands fa-twitter"></i>
          </a>
        </li>
        {props.loginuser ? (
          <li onClick={props.logOut} className="px-2">
            Logout
          </li>
        ) : (
          <>
            <li className="px-2">
              <NavLink to="/">Register</NavLink>
            </li>
            <li className="px-2">
              <NavLink to="/login">Login</NavLink>
            </li>
          </>
        )}
      </ul>
    </div>
  );
}
